<template>
  <div class="siteLoading" v-show="show">
    <img class="siteLoadingCenter" src="../assets/loading.gif"/>
  </div>
</template>
<script>
import CashDom from 'cash-dom'

export default {
  props: ['show'],
  watch: {
    show (isShow) {
      this.toggleBodyClass(isShow)
    }
  },
  mounted () {
    this.toggleBodyClass(this.show)
  },
  methods: {
    toggleBodyClass (isShow) {
      if (isShow) {
        CashDom(window.document.body).addClass('siteLoading-open')
      } else {
        CashDom(window.document.body).removeClass('siteLoading-open')
      }
    }
  }
}
</script>
<style lang="scss">

.siteLoading-open {
    overflow: hidden;
}

.siteLoading {
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background: #ffffff;
  z-index: 1;
  .siteLoadingCenter {
    position: absolute;
    width: 130px;
    height: 94px;
    left: 50%;
    top: 50%;
    margin-left: -65px;
    margin-top: -47px;
  }
}

.siteLoading.hide {
  opacity: 0;
  filter: alpha(opacity=0);
  visibility: hidden;
  transition: all 300ms cubic-bezier(0.165, 0.84, 0.44, 1);
  /* easeOutQuart */
}

</style>



