<template>
    <div class="about-container">
      <div class="about-card">
        <p><img class="logo" src="../assets/logo.png"/></p>
        <p class="inline-desc"><span class="inline-desc-large" v-for="integerItem in 24">-</span>&nbsp;{{ user.name }}&nbsp;<span class="inline-desc-large" v-for="integerItem in 24">-</span></p>
        <p class="desc">{{ user.profile }}</p>
        <p class="instagram">Say Hello</p>
        <p class="instagram-logo-line">
          <a target="_blank" :href="user.instagram" class="instagram-logo"><img class="instagram-logo" src="../assets/instagram.png"/></a>
        </p>
        <p class="user-email">e-mail: {{ user.email }}</p>
        <img class="picture" :class="{ 'invisible': !user.picture }" :src="user.picture" />
      </div>
    </div>
</template>
<script>
export default {
  name: 'about',
  data () {
    return {
      loading: true,
      user: {}
    }
  },
  beforeMount () {
    this.$store.dispatch('FETCH_USER').then((user) => {
      this.user = user
      this.loading = false
    })
  }
}
</script>
<style lang="scss">

@import "../_function.scss";

@font-face {
  font-family: 'Adele-light-webfont';
  src: url('../assets/font/adele-light-webfont/adele-light-webfont.eot');
  src: local(''), url('../assets/font/adele-light-webfont/adele-light-webfont.woff') format('woff'), url('../assets/font/adele-light-webfont/adele-light-webfont.ttf') format('truetype'), url('../assets/font/adele-light-webfont/adele-light-webfont.svg') format('svg');
  font-weight: normal;
  font-style: normal;
}

.about-card {
  font-family: 'Adele-light-webfont', -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, "Helvetica Neue", Helvetica, "PingFang SC", "Hiragino Sans GB", sans-serif;
  font-weight: 300;
  font-style: normal;
  line-height: 1.4;
  font-size: pxTorem(24px);
  margin: 0 auto;
  width: 995px;
  text-align: center;
  padding: 0 0 32px;
  .logo {
    margin: 0;
    padding: 0;
    width: 84px;
    height: 60px;
  }
  p {
    margin: 0;
    padding: 0;
  }
  .title {
    padding: 8px 0;
  }

  .inline-desc {
    text-transform: uppercase;
    line-height: 40px;
    margin: 0px;
    .inline-desc-large {
      font-size: pxTorem(32px);
    }
  }

  .desc {
    font-size: pxTorem(18px);
    padding: 0 0 8px;
    margin-bottom: 56px;
  }

  .instagram-logo-line {
    display: block;
    text-align: center;
    height: 40px;
  }

  .instagram-logo {
    display: inline-block;
    width: 40px;
    height: 40px;
  }

  .user-email {
    font-size: pxTorem(20px);
    margin-bottom: 12px;
  }

  .picture {
    width: 396px;
    height: 482px;

    &.invisible {
      visibility: hidden;
    }
  }

}

</style>



