<template>
  <div id="app">
    <div class="siteHeader">
      <h2><router-link to="/">copyright&copy; 2011-2017 MOMO SONG, all rights reserved.</router-link></h2>
      <div class="line"></div>
      <div class="rightBlock">
        <h1><router-link to="photography" :class="{ 'active-menu': isPhotoActive }">Photography</router-link></h1>
        <div class="nav"><router-link to="design" :class="{ 'active-menu': isDesignActive }">Design</router-link><span></span><router-link to="profile" :class="{ 'active-menu': isUserActive }">Profile</router-link></div>
      </div>
    </div>
    <transition name="fade" mode="out-in">
        <router-view class="view"></router-view>
    </transition>
  </div>
</template>

<script>
import { mapGetters } from 'vuex'

export default {
  name: 'app',
  computed: {
    ...mapGetters([ 'isDesignActive', 'isPhotoActive', 'isUserActive' ])
  },
  beforeMount () {
    this.resizePage(window.document.documentElement, window)
  },
  methods: {
    resizePage (docEl, win) {
      var clientWidth = docEl.clientWidth
      if (!clientWidth || clientWidth > 1100) {
        docEl.style.fontSize = '16px'
        return
      }
      docEl.style.fontSize = '20px'
    }
  }
}
</script>

<style lang="scss">

@import "./_variables.scss";
@import "./_function.scss";

@font-face {
  font-family: 'Bodoni';
  font-style: normal;
  font-weight: 400;
  src: url(./assets/font/Bodoni/Bodoni.eot);
  src: local('Bodoni'), url(./assets/font/Bodoni/Bodoni.eot?#iefix) format('embedded-opentype'), url(./assets/font/Bodoni/Bodoni.woff) format('woff'), url(./assets/font/Bodoni/Bodoni.ttf) format('truetype'), url(./assets/font/Bodoni/Bodoni.svg) format('svg');
}

@font-face {
  font-family: 'BodoniStd-Italic';
  font-style: normal;
  font-weight: 400;
  src: url(./assets/font/Bodoni/BodoniStd-Italic.eot);
  src: local('BodoniStd-Italic'), url(./assets/font/Bodoni/BodoniStd-Italic.eot?#iefix) format('embedded-opentype'), url(./assets/font/Bodoni/BodoniStd-Italic.woff) format('woff'), url(./assets/font/Bodoni/BodoniStd-Italic.ttf) format('truetype'), url(./assets/font/Bodoni/BodoniStd-Italic.svg) format('svg');
}

* {
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}

html, body {
  font-size: $browser-default-font-size;
  height: 100%;
  background: #fff;
}

html {
  -webkit-overflow-scrolling: touch;
  -webkit-text-size-adjust: 100%;
  -ms-text-size-adjust: 100%;
  height: 100%;
  overflow-y: scroll;
}

body {
  font-family: "Source Han Sans", "", "Hiragino Sans GB", "", "Microsoft YaHei", "-apple-system", "BlinkMacSystemFont", "Segoe UI", "Roboto", "Helvetica Neue", "Helvetica", "PingFang SC", "Hiragino Sans GB", "Microsoft YaHei", "SimSun", sans-serif;
  line-height: 1.6;
  -webkit-font-smoothing: antialiased;
  /* Fix for webkit rendering */
  -webkit-text-size-adjust: 100%;
  font-style: italic;
  margin: 0;
}

h1, h2, h3, h4, h5, h6 {
  font-size: 1rem;
  font-weight: normal;
  margin: 0;
}

a {
    text-decoration: none;
    color: #16161f;
    &:hover {
      color: #16161f;
    }
    &:visited {
      color: #16161f;
    }
}

.card {
  box-shadow: 0 2px 7px rgba(0, 0, 0, 0.2);
}

.align-center {
  text-align: center
}

#app {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.siteHeader {
  height: 100px;
  margin: 0 auto;
  *zoom: 1;
  padding: 100px 0;
  display: table;
  text-align: center;
  &:after {
    content: ".";
    display: block;
    clear: both;
    height: 0;
    visibility: hidden;
    overflow: hidden;
  }
  h2 {
    font-family: "BodoniStd-Italic", "Source Han Sans", "", "Hiragino Sans GB", "", "Microsoft YaHei", "-apple-system", "BlinkMacSystemFont", "Segoe UI", "Roboto", "Helvetica Neue", "Helvetica", "PingFang SC", "Hiragino Sans GB", "Microsoft YaHei", "SimSun", serif;;
    font-size: pxTorem(18px);
    display: table-cell;
    vertical-align: middle;
    height: 100px;
    line-height: 100px;
    a {
      color: #16161f;
    }
  }
  .line {
    background: url(./assets/line.png) no-repeat;
    background-size: 100px 100px;
    background-position: 8px 0px;
    display: table-cell;
    vertical-align: middle;
    width: 90px;
    height: 100px;
    line-height: 100px;
  }
  .rightBlock {
    display: table-cell;
    vertical-align: middle;
    font-family: "BodoniStd-Italic", "Source Han Sans", "", "Hiragino Sans GB", "", "Microsoft YaHei", "-apple-system", "BlinkMacSystemFont", "Segoe UI", "Roboto", "Helvetica Neue", "Helvetica", "PingFang SC", "Hiragino Sans GB", "Microsoft YaHei", "SimSun", serif;;
    font-size: pxTorem(18px);
    font-weight: 100;
    a.active-menu {
      color: #656575;
    }
    h1 {
      text-align: center;
      font-size: pxTorem(18px);
      a {
        color: #16161f;
      }
    }
    .nav {
      span {
        padding: 0 12px;
      }
    }
  }
}

</style>



